* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.spinner_header {
    padding: 1.5rem 3rem;
    display: flex;
    align-items: center;
    gap: 10px;
    color: #fff;
}

.spinner_header_link {
    color: #fff;
    font-size: 1.4rem;
}

.example {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-auto-rows: 130px;
}

.example>* {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn {
    border: 0px;
    background: none;

    right: 1rem;
    position: fixed;
    bottom: 1rem;
}

.btn:hover,
.showcode-button:hover {
    cursor: pointer;
}

.showcode-button {
    padding: 0.5rem 1rem;
    border: none;
    background: #e4e4e7;
}

.code {
    display: none;
}

.header {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-auto-rows: 40px;
}

.header>* {
    display: flex;
    align-items: center;
    justify-content: center;
}

