.main .demo {
    display: none;
}

.main:hover .loader {
    display: none;
}

.main:hover .demo {
    display: block;
}

.full-screen-demo {
    height: 100vh;
    width: 100%;
    background-color: #d5d5d5AB;
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    align-items: center;
    justify-content: center;
}
